.outer-box {
  background-color: white;
  color: black;
  z-index: 1;
}

.inner-box {
  width: 100%;
  height: 100%;
  border: 1px solid black;
  background-color: pink;
  color: black;
  z-index: 1;

  display: flex; 
  align-items: center; 
  justify-content: center;
}

.selected {
  border: 3px dotted #227b4f;
}

.base {
  border: 1px solid black;
}